<template>
  <!-- 账号管理 -->
  <div>
    <v-container grid-list-md text-xs-center>
      <v-layout row wrap="">
        <v-snackbar
          v-model="snackbar"
          :bottom="y === 'bottom'"
          :color="alertcolor"
          :left="x === 'left'"
          :multi-line="mode === 'multi-line'"
          :right="x === 'right'"
          :timeout="timeout"
          :top="y === 'top'"
          :vertical="mode === 'vertical'"
          >{{ text }}</v-snackbar
        >
        <!-- 下面的数据 -->
        <!-- <p>{{count}}</p> -->
        <!-- 台架 -->
        <v-flex xs12>
          <v-card color="rgba(238,238,238)">
            <v-card-text class="px-0">
              <!-- 具体的值 -->
              <template>
                <div>
                  <v-toolbar flat color="#eee">
                    <v-flex xs12 sm6 md3>台架管理</v-flex>
                    <v-btn id="add" @click="dialog_add = true">
                      <i class="fas fa-plus"></i>添加
                    </v-btn>
                    <v-dialog persistent v-model="dialog_add" max-width="500px">
                      <v-card>
                        <!-- 标题 -->
                        <v-card-title
                          class="headline"
                          v-text="defaultItem.id ? '编辑台架' : '新增台架'"
                        ></v-card-title>
                        <!-- 内容 -->
                        <v-card-text>
                          <v-container fluid grid-list-md>
                            <v-layout row wrap="">
                              <v-flex d-flex xs12 sm12 md12>
                                <v-card-text>
                                  <v-form ref="form">
                                    <!--                                    <v-text-field-->
                                    <!--                                      v-model="defaultItem.platformId"-->
                                    <!--                                      label="台架ID"-->
                                    <!--                                      :rules="idRules"-->
                                    <!--                                    ></v-text-field>-->
                                    <v-text-field
                                      v-model="defaultItem.platformName"
                                      label="台架名称"
                                      :rules="nameRules"
                                    ></v-text-field>
                                  </v-form>
                                </v-card-text>
                              </v-flex>
                            </v-layout>
                          </v-container>
                        </v-card-text>
                        <!-- 按钮 -->
                        <v-card-actions>
                          <v-spacer></v-spacer>
                          <v-btn
                            color="green darken-1"
                            flat="flat"
                            @click="cancel()"
                            >取消</v-btn
                          >
                          <v-btn
                            color="green darken-1"
                            flat="flat"
                            @click="add_save()"
                            >保存</v-btn
                          >
                        </v-card-actions>
                      </v-card>
                    </v-dialog>
                  </v-toolbar>
                  <!-- 页面数据以及分页 -->
                  <v-data-table
                    :headers="headers"
                    :items="datalist"
                    :pagination.sync="pagination"
                    rows-per-page-text="每页条数"
                    :rows-per-page-items="rowsPerPageItems"
                  >
                    <template v-slot:items="props">
                      <td class="text-xs-center">
                        {{ props.item.platformId }}
                      </td>
                      <td class="text-xs-center">
                        {{ props.item.platformName }}
                      </td>
                      <td class="text-xs-center">
                        {{ props.item.platformIp }}
                      </td>
                      <td class="text-xs-center">{{ props.item.parsePort }}</td>
                      <td class="text-xs-center">
                        {{ props.item.online ? '在线' : '离线' }}
                      </td>
                      <td class="text-xs-center">
                        {{ props.item.is_conn ? '连接' : '未连接' }}
                      </td>
                      <td class="text-xs-center">{{ props.item.create_at }}</td>
                      <td class="text-xs-center">{{ props.item.app_key }}</td>
                      <td class="text-xs-center">
                        {{ props.item.app_secret }}
                      </td>
                      <td class="text-xs-center">
                        <v-icon
                          small
                          class="f_green"
                          @click="editItem(props.item)"
                          >编辑</v-icon
                        >
                        <v-icon
                          small
                          class="f_green"
                          @click="deleteItem(props.item.platformId)"
                          >删除</v-icon
                        >
                      </td>
                    </template>
                    <template v-slot:no-data>
                      <p>暂无符合条件的选项</p>
                    </template>
                  </v-data-table>
                </div>
              </template>
            </v-card-text>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  data: () => ({
    // 排序
    pagination: {
      sortBy: 'carId',
      descending: true
    },
    // 弹出框
    dialog: false,
    dialog_add: false,
    snackbar: false,
    y: 'top',
    alertcolor: '',
    x: null,
    mode: '',
    timeout: 3000,
    text: '',
    // 表单验证
    // idRules: [
    //   (v) => !!v || '台架ID不可为空',
    //   (v) => /^[0-9]{3,6}$/.test(v) || '台架ID格式错误（应为3-6位纯数字）'
    // ],
    nameRules: [
      (v) => !!v || '台架名称不可为空',
      (v) =>
        /^[\u4E00-\u9FA5a-zA-Z0-9]{1}[\u4E00-\u9FA5a-zA-Z0-9_]{2,7}$/.test(v) ||
        '台架名称格式错误'
    ],

    id: '',
    // 分页
    rowsPerPageItems: [5, 10],
    date: new Date().toISOString().substr(0, 10),
    staffitems: ['admin', '人员 车辆', '人员运维', '车辆管理'],
    headers: [
      {
        text: '	ID',
        value: 'platformId'
      },
      {
        text: '名称',
        value: 'platformName'
      },
      {
        text: '	解析IP',
        value: 'platformIp'
      },

      {
        text: '	解析端口',
        value: 'parsePort'
      },

      {
        text: '	在线状态 ',
        value: 'online'
      },
      {
        text: '连接状态 ',
        value: 'is_conn'
      },
      {
        text: '创建时间',
        value: 'create_at'
      },
      {
        text: 'App_key ',
        value: 'app_key'
      },
      {
        text: 'App_密钥',
        value: 'app_secret'
      },
      {
        text: '操作 ',
        value: ''
      }
    ],
    defaultItem: {
      id: '',
      // platformId: '',
      platformName: ''
    },
    datalist: [
      {
        id: '',
        platformId: '',
        platformName: '',
        online: '',
        is_conn: '',
        create_at: '',
        app_key: ''
      }
    ],
    page: '1'
  }),
  watch: {
    dialog(val) {
      val || this.close()
    }
  },

  sockets: {
    connect: function() {
      this.id = this.$socket.id
    },
    server_response: function(res) {
      console.log(res)
      // console.log(res)

      let deviceid = res.data.deviceid
      if (res.data.type == '2') {
        console.log(123)
        this.search_msg(deviceid)
      } else {
        console.log(345)
      }
    }
  },

  created() {
    this.initialize()
  },

  methods: {
    // 获取用户列表数据
    initialize() {
      this.$axios({
        method: 'get',
        url: 'getPlatformlist/'
      })
        .then((res) => {
          let data = res.data.data
          this.datalist = data
          // 将返回信息中的carid和is_conn放进data中的对象中，注意每次进来以后清空，再push新的一组
          this.plat_save(this.datalist)
        })
        .catch((res) => {
          let error = res.message
          if (error.indexOf('Network') !== -1) {
            this.snackbar = true
            this.alertcolor = 'error'
            this.text = '网络异常'
          }
        })
      // let token = this.$cookies.get('session')
    },
    editItem(item) {
      this.getItem(item.platformId)
    },
    getItem(id) {
      this.$axios({
        method: 'get',
        url: 'getPlatforminfo/',
        params: {
          platformId: id
        }
      }).then((res) => {
        if (res.data.code === 200) {
          this.defaultItem.id = res.data.data.id
          // this.defaultItem.platformId = res.data.data.platformId
          this.defaultItem.platformName = res.data.data.platformName
          this.dialog_add = true
        } else {
          this.snackbar = true
          this.alertcolor = 'error'
          this.text = res.data.msg
        }
      })
    },
    deleteItem(item) {
      if (confirm('你确定删除吗?')) {
        this.$axios({
          method: 'get',
          url: 'PlatformDelete/',
          params: {
            platformId: item
          }
        }).then((res) => {
          if (res.data.code === 200) {
            this.snackbar = true
            this.alertcolor = 'green'
            this.text = res.data.msg
            this.initialize()
          } else {
            this.snackbar = true
            this.alertcolor = 'error'
            this.text = res.data.msg
          }
        })
      }
    },
    // 新增保存
    add_save() {
      let validate = this.$refs.form.validate()
      if (validate === false) {
        return false
      }
      if (this.defaultItem.id) {
        this.$axios({
          method: 'post',
          url: 'updatePlatformInfo/',
          data: {
            id: this.defaultItem.id,
            // platformId: this.defaultItem.platformId,
            platformName: this.defaultItem.platformName
          },
          transformRequest: [
            function(data) {
              let ret = ''
              for (let it in data) {
                ret +=
                  encodeURIComponent(it) +
                  '=' +
                  encodeURIComponent(data[it]) +
                  '&'
              }
              return ret
            }
          ],
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        }).then((res) => {
          if (res.data.code === 200) {
            this.snackbar = true
            this.alertcolor = 'green'
            this.text = res.data.msg
            this.dialog_add = false
            this.defaultItem.id = ''
            this.$refs.form.reset()
            this.initialize()
          } else {
            this.snackbar = true
            this.alertcolor = 'error'
            this.text = res.data.msg
          }
        })
      } else {
        this.$axios({
          method: 'post',
          url: 'PlatformAdd/',
          data: {
            // platformId: this.defaultItem.platformId,
            platformName: this.defaultItem.platformName
          },
          transformRequest: [
            function(data) {
              let ret = ''
              for (let it in data) {
                ret +=
                  encodeURIComponent(it) +
                  '=' +
                  encodeURIComponent(data[it]) +
                  '&'
              }
              return ret
            }
          ],
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        }).then((res) => {
          if (res.data.code === 200) {
            this.snackbar = true
            this.alertcolor = 'green'
            this.text = res.data.msg
            this.dialog_add = false
            this.defaultItem.id = ''
            this.$refs.form.reset()
            this.initialize()
          } else {
            this.snackbar = true
            this.alertcolor = 'error'
            this.text = res.data.msg
          }
        })
      }
    },
    cancel() {
      this.dialog_add = false
      this.defaultItem.id = ''
      this.$refs.form.reset()
    },

    plat_save(msg) {
      // 获取所有台架信息传导store

      this.$store.commit('plat_msg', msg)
    },
    search_msg(deviceid) {
      this.$axios({
        method: 'get',
        url: 'getPlatforminfo/',
        params: {
          platformId: deviceid
        }
      }).then((res) => {
        this.change_msg(res.data)
      })
    },
    change_msg(res) {
      // 将已有数据存入一个数组
      var over_msg = []
      for (var i = 0; i < this.datalist.length; i++) {
        over_msg.push(this.datalist[i].id)
      }
      var return_msg = res.data.id
      // 如果包含
      if (over_msg.indexOf(return_msg) > -1) {
        // console.log('已存在')
        // console.log(this.datalist)
        // 已经存在的话，遍历原来的，替换
        for (var j = 0; j < this.datalist.length; j++) {
          if (this.datalist[j].id == res.data.id) {
            this.datalist[j] = res.data
            this.datalist.splice(j, 1, res.data)
          }
        }
        console.log(this.datalist)
      } else {
        // 不存在的话，在结尾追加
        let data_lengths = this.msg_arr.length
        this.datalist[data_lengths] = res.data
        this.datalist.splice(data_lengths, 1, res.data)
      }
    }
  },
  computed: mapGetters({
    count: 'countnew'
  })
}
</script>
<style>
tr {
  background-color: #eee;
}

th {
  background-color: #ebf5ff !important;
}

.v-datatable thead th.column.sortable {
  text-align: center !important;
}

.v-table__overflow {
  width: 100%;
  /*overflow-x: hidden;*/
}

.theme--light.v-datatable .v-datatable__actions {
  background-color: #eee;
}
</style>
<style scoped>
/* 按钮添加颜色 */
#add {
  color: #4caf50 !important;
  background-color: #eee;
}

.v-btn:not(.v-btn--depressed):not(.v-btn--flat) {
  will-change: box-shadow;
  -webkit-box-shadow: none;
  box-shadow: none;
}

i {
  font-size: 20px;
  margin: 0 5px 0 -10px;
}

/* 编辑删除 */
.f_green {
  color: #4caf50;
  margin-right: 20px !important;
  font-style: normal;
}

/deep/ tbody tr:nth-child(2n) {
  background-color: #f5fafe !important;
}

/* 搜索框 */
/deep/.v-toolbar:not(.v-toolbar--fixed) .v-toolbar__content {
  margin-left: 0px;
}

/deep/.v-table .v-datatable__actions {
  display: -webkit-box !important;
  -webkit-box-pack: center !important;
}

/*解决表格文本不会自动换行的问题*/
.v-table .text-xs-center {
  word-break: break-word;
}
</style>
